.circle-preloader {
	display: block;
	width: 100px;
	height: 100px;
	border: 4px solid #444;
	border-bottom-color: #888;
	
	border-radius: 50%;
	
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	
	animation: spin .75s infinite linear;

	display: none;
}

@keyframes spin {
	100% {
		transform: rotate(360deg);
	}
}